import React, { useEffect } from 'react';
import Writer from 'typewriter-effect';

interface Props {
    onInit?: (param:any) => void
}

const Typewriter = (props:Props) => {
    const { onInit } = props
    // typewriter => {
    //     typewriter
    //     .typeString(text)
    //     .start();
    // }
    return(
        <Writer
            options={{
                delay: 20,
                cursor: ""
            }}
            onInit={onInit}
        />
    )
}

export default Typewriter