import { Box } from '@mui/material'
import logo from '../../styles/images/logo.png'

const images: any = {
    logo
}
interface Props {
    name?: string,
    size?: number | string,
    h?: number | string,
    w?: number | string,
}
export const Icon = ( props: Props ) => {
    const { name="logo", size=30, h, w} = props
    return(
        <>
            <Box width={w??size} height={h??size} p={2} >
                <img style={{ width: "100%" }} src={images[name]} alt={name} />
            </Box>
        </>
    )
}