import { Box, Divider, Grid, Paper, Slide, Stack, useTheme } from "@mui/material"
import React from "react"
import Scroll from "../../class/Scroll"
import Container from "../../components/Container"
import SubTitle from "../../components/SubTitle"
import DataObjectRoundedIcon from '@mui/icons-material/DataObjectRounded';
import StorageRoundedIcon from '@mui/icons-material/StorageRounded';
import LanguageRoundedIcon from '@mui/icons-material/LanguageRounded';

interface Props{ }

const WhatWeDo = (props:Props) => {
    const [animation, setAnimation] = React.useState<boolean>(false)
    const reference = React.useRef()
    const theme = useTheme()
    React.useEffect(() => {
        const elem = document.getElementById("description-whatWeDo")
        Scroll.getVisibleEment(elem, (elementPosition:number, scrollPosition:number) => {
            if(scrollPosition > elementPosition){
                setAnimation(true)
            }else{
                setAnimation(false)
            }
        })
    },[])
    return(
        <Container direction="column" >
            <Box id="description-whatWeDo" display="flex" justifyContent="center" ref={reference} overflow="hidden" >
                <Grid container >
                    <Grid item xs={12} >
                        <SubTitle title="¿QUE SABEMOS HACER?" activeTrasition={animation} directionTransition="down" />
                    </Grid>
                    <Grid item xs={12} >
                        <Stack p={4} direction={{
                            xs: "column",
                            sm: "column",
                            md: "row",
                            lg: "row",
                        }} divider={<Divider orientation="vertical" color={theme.palette.kikedevMain.main} flexItem sx={{ borderRightWidth: "medium" }} />} spacing={2} >
                            <Slide in={animation} direction="right" container={reference.current} style={{ transitionDelay: animation ? '700ms' : '0ms' }} >
                                <Paper elevation={6} sx={{
                                        borderRadius: 5,
                                        padding: 3
                                    }}>
                                    <Box textAlign="center" >
                                        <Box>
                                            <DataObjectRoundedIcon sx={{
                                                fontSize: "80px"
                                            }} />
                                        </Box>
                                        Lorem ipsum, dolor sit amet consectetur adipisicing elit. Rerum amet molestiae sed doloribus porro alias vitae blanditiis est? Saepe esse quis aspernatur deleniti ipsa dolores voluptates nihil hic beatae at.
                                    </Box>
                                </Paper>
                            </Slide>
                            <Slide in={animation} direction="up" container={reference.current} style={{ transitionDelay: animation ? '700ms' : '0ms' }} >
                                <Paper elevation={6} sx={{
                                        margin: 3,
                                        borderRadius: 5,
                                        padding: 3
                                    }}>
                                    <Box textAlign="center">
                                        <Box>
                                            <StorageRoundedIcon sx={{
                                                fontSize: "80px"
                                            }} />
                                        </Box>
                                        Lorem ipsum dolor sit amet consectetur adipisicing elit. Pariatur explicabo accusantium expedita libero dolore minus, vero deserunt similique quidem quaerat beatae commodi, repellendus cupiditate quod reprehenderit delectus ipsam quam eveniet.
                                    </Box>
                                </Paper>
                            </Slide>
                            <Slide in={animation} direction="left" container={reference.current} style={{ transitionDelay: animation ? '700ms' : '0ms' }} >
                                <Paper elevation={6} sx={{
                                        margin: 3,
                                        borderRadius: 5,
                                        padding: 3
                                    }}>
                                    <Box textAlign="center" >
                                        <Box>
                                            <LanguageRoundedIcon sx={{
                                                fontSize: "80px"
                                            }} />
                                        </Box>
                                        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Provident quia illo vel cupiditate quasi. Reprehenderit quas, obcaecati veritatis possimus porro nulla eos officiis ex, eligendi tempore, iusto quidem consequatur voluptatibus.
                                    </Box>
                                </Paper>
                            </Slide>
                        </Stack>
                    </Grid>
                </Grid>

            </Box>
        </Container>
    )
}

export default WhatWeDo