import { Box, TextField as Field } from "@mui/material";

interface Value {
    attr?: string | undefined,
    value: string | number,
}

interface Props {
    mb?: number,
    type?: string,
    multiline?: boolean,
    rows?: number,
    name?: string,
    label?: string,
    onChange?: (elem: Value) => void,
}

const TextField = (props: Props) => {
    const { label, onChange, name, mb=2, multiline=false, rows, type="text" } = props
    const handlerChange = (elem:React.ChangeEvent<HTMLInputElement>) => onChange && onChange({attr:elem.target.name, value:elem.target.value})
    return(
        <Box display="inline-flex" width="100%" mb={mb} >
            <Field
                type={type}
                multiline={multiline}
                rows={rows}
                size="medium"
                fullWidth
                name={name}
                label={label}
                onChange={handlerChange}
            />
        </Box>
    )
}

export default TextField