import Container from "../../components/Container"
import { Box, Divider, Fade, Grid, Link, Paper, Slide } from "@mui/material"
import Image from "../../components/Image"
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import kike from '../../styles/images/me.jpg'
import React from "react";
import Scroll from "../../class/Scroll";
import Typewriter from "../../components/Typewriter";

const Profile = () => {
    const [animation, setAnimation] = React.useState<boolean>(false)
    const reference = React.useRef()
    React.useEffect(() => {
        const elem = document.getElementById("profile-section")
        Scroll.getVisibleEment(elem, (elementPosition:number, scrollPosition:number) => {
            if(scrollPosition > elementPosition+900){
                setAnimation(true)
            }else{
                setAnimation(false)
            }
        })
    },[])

    return(
        <Box component="span" id="profile-section" >
            <Container>
                <Grid container>
                    <Grid item sx={{
                        display: "flex",
                        justifyContent: "center",
                    }} >
                        <Paper elevation={6} sx={{
                                    margin: 3,
                                    borderRadius: 5,
                                    padding: 3,
                                    maxWidth: "80%"
                                }}>
                            <Box ref={reference} display="flex" sx={{
                                flexDirection:{
                                    xs: "column",
                                    sm: "column",
                                    md: "row",
                                    lg: "row",
                                }                                
                            }} >
                                <Fade in={animation} style={{ transitionDelay: animation ? '500ms' : '0ms' }} >
                                    <Box component="div" display="flex" margin="auto" height="100%" borderRadius="50%" overflow="hidden"
                                        sx={{
                                            maxWidth:{
                                                xs: "100px",
                                                sm: "100px",
                                                md: "200px",
                                                lg: "200px",
                                            }
                                        }}
                                    >
                                        <Image url={kike} size="100%" height="auto" />
                                    </Box>
                                </Fade>
                                <Box marginLeft={3} overflow="hidden" >
                                    <Box sx={{
                                            textAlign:{
                                                xs: "center",
                                                sm: "center",
                                                md: "left",
                                                lg: "left",
                                            }                                
                                        }} >
                                        <Slide in={animation} direction="left" style={{ transitionDelay: animation ? '500ms' : '0ms' }} >
                                            <Box fontWeight="500"
                                                sx={{
                                                    fontSize:{
                                                        xs: "1.2rem",
                                                        sm: "1.4rem",
                                                        md: "2rem",
                                                        lg: "2rem",
                                                    }                                
                                                }}
                                            >
                                                Enrique A. Pacheco
                                            </Box>
                                        </Slide>
                                        <Slide in={animation} direction="left" style={{ transitionDelay: animation ? '700ms' : '0ms' }}>
                                            <Box fontWeight="500" color="kikedevMain.main"
                                                sx={{
                                                    fontSize:{
                                                        xs: ".8rem",
                                                        sm: ".8rem",
                                                        md: "1rem",
                                                        lg: "1rem",
                                                    }                                
                                                }}
                                            >
                                                WEB DEVELOPER
                                            </Box>
                                        </Slide>
                                        <Slide in={animation} direction="left" style={{ transitionDelay: animation ? '900ms' : '0ms' }}>
                                            <Box display="flex" alignItems="center" >
                                                <LinkedInIcon
                                                    sx={{
                                                        fontSize: "30px",
                                                        color:"#0e76a8 "
                                                    }}
                                                />
                                                <Link href="https://www.linkedin.com/in/kikepacheco/" target="_blank" >@kikepacheco</Link>
                                            </Box>
                                        </Slide>
                                    </Box>
                                    <Divider flexItem sx={{ borderRightWidth: "medium" }} />
                                    <Box fontWeight="400" marginTop={2}
                                        sx={{
                                            fontSize:{
                                                xs: ".8rem",
                                                sm: ".8rem",
                                                md: "1rem",
                                                lg: "1rem",
                                            }                                
                                        }}
                                    >
                                    {animation&&
                                        <Typewriter
                                            onInit={typewriter => {
                                                typewriter
                                                .typeString("Soy desarrollador de sitios web con amplios conocimientos de JavaScript, soy entusiasta de la tecnología y actualmente emprendedor en el ambiente del desarrollo de software buscando crear vínculos profesionales y satisfactorios con los clientes y sus proyectos, ya que cualquier idea puede hacerse realidad es mi propósito desarrollar esas ideas bajos los más altos estándares del desarrollo utilizando los lenguajes de programación modernos para obtener resultados satisfactorios.")
                                                .start();
                                            }}
                                        />
                                    }
                                    </Box>
                                </Box>
                            </Box>
                        </Paper>
                    </Grid>
                </Grid>
            </Container>
        </Box>
    )
}

export default Profile