import { Box } from "@mui/material"

interface Props {
    size?: string | number,
    width?: string | number,
    height?: string | number,
    url?: string,
}

const Image = (props: Props) => {
    const { size=100, width, height, url } = props
    return(
        <Box display="inline-flex" width={width??size} height={height??size} >
            <img style={{ width: "100%" }} src={url} alt={url} />
        </Box>
    )
}

export default Image