import { Box, Grid, Paper } from "@mui/material"
import Container from "../../components/Container"
import Image from "../../components/Image"
import Typewriter from "../../components/Typewriter"
import pghome from '../../styles/images/pghome.png'

const Main = () => {
    const fontResponsive: object = {
        lg: "2rem",
        md: "2rem",
        sm: "2rem",
        xs: "15px",
    }
    return(
        <Container>
            <Grid container spacing={2} >
                <Grid item xs={12} sm={12} md={6} lg={6} sx={{ height: "100%", display: "flex", alignItems: "center", justifyContent:"center" }}>
                    <Paper elevation={6} sx={{
                        margin: 3,
                        borderRadius: 5,
                        padding: 3
                    }} >
                        <Box display="flex" flexDirection="column" fontWeight="bold" color="kikedevMain.main" sx={{
                                    fontSize : fontResponsive
                                }}>
                            <Box display="flex" flexDirection="row" >
                                <Box component="span" marginRight={2} sx={{
                                    fontSize : fontResponsive
                                }} >bash ~ $</Box>
                                <Typewriter
                                    onInit={typewriter => {
                                        typewriter
                                        .pauseFor(500)
                                        .typeString("kikedev start")
                                        .start();
                                    }}
                                />
                            </Box>
                            <Box display="flex" fontWeight="500" color="#fff"
                                sx={{
                                    fontSize : {
                                        lg: "1.5rem",
                                        md: "1.5rem",
                                        sm: "1.5rem",
                                        xs: "15px",
                                    }
                                }}
                            >
                                <Typewriter
                                    onInit={typewriter => {
                                        typewriter
                                        .pauseFor(2000)
                                        .typeString("Especialistas en desarrollo web y aplicaciones móviles <br />")
                                        .pauseFor(500)
                                        .typeString("Mejor tiempo de desarrollo<br />")
                                        .pauseFor(500)
                                        .typeString("Soluciones óptimas y especializadas")
                                        .start();
                                    }}
                                />
                            </Box>
                            <Typewriter
                                onInit={typewriter => {
                                    typewriter
                                    .pauseFor(7500)
                                    .typeString("end script")
                                    .start();
                                }}
                            />
                        </Box>
                    </Paper>
                </Grid>
                <Grid item md={6} lg={6} sx={{
                    display:{
                        xs: "none",
                        sm: "none",
                        md: "block",
                        lg: "block",
                    },
                    height: "100%"
                }} >
                    <Image url={pghome} size="100%" />
                </Grid>
            </Grid>
        </Container>
    )
}

export default Main