import React from "react"
import { Box, Slide, Typography, useTheme } from "@mui/material"

interface Props {
    title: string,
    activeTrasition?: boolean,
    directionTransition?: "right" | "left" | "up" | "down",
}


const SubTitle = (props: Props) => {
    const [render, setRender] = React.useState<React.ReactNode>(<></>)
    const node = React.useRef()
    const { title, activeTrasition=false, directionTransition="left" } = props
    const theme = useTheme()
    const styles: object = {
        fontSize: "2rem",
        fontWeight: "bold",
    }
    React.useEffect(() => {
        const parts = title?.split(" ")
        if( parts?.length > 1 ){
            const textB = parts[parts.length-1]
            parts.pop()
            const textA = parts.join(" ")
            const value = <> {textA} <Typography sx={{ marginLeft: 1, textDecoration: "underline", textDecorationColor: theme.palette.kikedevMain.main, ...styles, textDecorationThickness: "6px", textUnderlineOffset: "5px" }} >{textB}</Typography> </>
            setRender(value)
        }else{
            setRender(<>{parts.join(" ")}</>)
        }
    },[title])
    return(
        <Box overflow="hidden" >
            <Slide in={activeTrasition} direction={directionTransition} >
                <Box ref={node} display="flex" justifyContent="center" mb={2} {...styles} >
                    { render }
                </Box>
            </Slide>
        </Box>
    )
}

export default SubTitle