import { createTheme } from '@mui/material/styles';

const theme = createTheme({
    palette: {
        mode: 'dark',
        kikedevMain: {
            main : "#00F461",
        }
    },
});

interface CustomPalette{
    main: string
}

declare module '@mui/material/styles' {
    interface Palette {
        kikedevMain: CustomPalette ;
    }
    interface PaletteOptions {
        kikedevMain: CustomPalette ;
    }
}

export default theme