export default class Scroll{
    constructor(){}
    static getVisibleEment(elment:any, callback:any){
        Scroll.onWindowScroll((scrollPosition:number) => {
            Scroll.getElementOffsetHeight(elment, callback, scrollPosition)
        })
    }
    static getElementOffsetHeight(element:HTMLElement, callback:any, windowScrollPosition?:number){
        if( callback ){
            callback(element.getBoundingClientRect().y, windowScrollPosition)
        }
    }
    static onWindowScroll(callback:any){
        window.addEventListener("scroll",() => {
            if( callback ){
                const size: number = window.scrollY
                callback(size)
            }
        })
    }
}